import React from "react";
import { useSelector } from "react-redux";
import DragAndDropComponent from "../components/DragAndDrop/DragAndDrop";
import ReusableTable from "../components/Table/Table";
import CardComponent from "../components/Card/Card";

const RevisionFloat = () => {
  const droppedObjectData = useSelector((state) => state.droppedObject.data);

  const columns = [
    {
      accessorKey: "id",
      header: "ID",
    },
    {
      accessorKey: "name",
      header: "Name",
    },
    {
      accessorKey: "type",
      header: "Type",
    },
  ];

  const sampleData = [
    {
      title: droppedObjectData?.displayName || "No Title",
      text: droppedObjectData?.objectType || "No Type",
      imageUrl: "https://via.placeholder.com/150",
      footerText: "More Details",
      buttonLabel: "View",
    },
  ];

  return (
    <div>
      <DragAndDropComponent />
      {droppedObjectData && (
        <>
          <ReusableTable data={[droppedObjectData]} columns={columns} />
          {sampleData.map((card, index) => (
            <CardComponent
              key={index}
              title={card.title}
              text={card.text}
              imageUrl={card.imageUrl}
              footerText={card.footerText}
              buttonLabel={card.buttonLabel}
            />
          ))}
        </>
      )}
    </div>
  );
};

export default RevisionFloat;
